        <div class="photo" :style="{'backgroundImage': `url(${userData.photoURL})`}"></div>
        <div class="name"><p>{{userData.displayName}}</p></div>
        <div class="bio" v-if="userData.detail">
          <p v-if="userData.detail.talent">音樂專長：{{userData.detail.talent}}</p>
          <p v-else>音樂專長：不公開</p>
          <p v-if="userData.detail.style">曲風：{{userData.detail.style}}</p>
          <p v-else>曲風：不公開</p>
        </div>
        <div class="bio" v-else>
          <p>音樂專長：不公開</p>
          <p>曲風：不公開</p>
        </div>
        <button class="btn btn-outline-light" data-toggle="modal" data-target="#detail-modal">詳細資訊</button>
        <div class="editors"
          :class="{'menuSticky': tempDom.menuSticky}">
          <a href="personal-postmusic.html" class="links">
            <i class="fas fa-plus-circle"></i> 發佈音樂
          </a>
          <a href="personal-managepost.html" class="links">
            <i class="fas fa-cog"></i> 管理貼文
          </a>
          <a href="personal-manageaccount.html" class="links">
            <i class="fas fa-user-cog"></i>編輯帳號
          </a>
        </div>
      </div>
      <div class="col-lg-9 col-md-8 right-content">
        <router-view :userData="userData"></router-view>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="detail-modal" tabindex="-1" role="dialog" aria-labelledby="detailModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="detailModalLabel"><i class="fas fa-info-circle"></i> 詳細資訊</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-6"><p><i class="fas fa-user"></i>姓名：<b>曾霈宭</b></p></div>
              <div class="col-md-6"><p><i class="fas fa-venus-mars"></i>性別：<b>女</b></p></div>
              <div class="col-md-6"><p><i class="fas fa-map-marker-alt"></i> 現居：<b>高雄市</b></p></div>
              <div class="col-md-6"><p><i class="fas fa-seedling"></i> 生日：<b>85/09月</b></p></div>
              <div class="col-md-12"><p><i class="far fa-id-card"></i> 現在任職：<b>學生</b></p></div>
              <div class="col-md-12"><p><i class="fas fa-music"></i> 音樂專長：<b>吉他、電吉他</b></p></div>
              <div class="col-md-12"><p><i class="fas fa-headphones"></i> 曲風：<b>搖滾、爵士</b></p></div>
              <div class="col-md-12">
                <p><i class="fas fa-universal-access"></i> 自我介紹：
                  <pre class="pre" v-linkified>
                  </pre>
                </p>
              </div>
              <hr>
              <div class="col-md-12">
                <p class="bd-n"><i class="far fa-paper-plane"></i> 聯絡方式：
                  <pre class="pre" v-linkified>
                  </pre>
                </p>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-orange" id="filter-btn" data-dismiss="modal">確定</button>
          </div>
        </div>
      </div>
    </div>	
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  data() {
    return {      
      userData: null,
      tempDom: {
        menuSticky: false, 
      }
    }
  },
  methods: {    
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          })
        } else {
          vm.userData = null;
        }
      });
    },
    scrollMethod(scrollValue) {
      const vm = this;
      
    }
  },
  created() {
    const vm = this;
    vm.getUserData();
    
  },
}
</script>

<style lang="scss" scoped>
