  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(../assets/img/bg/wave-bg.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 270%;
  height: 100vh;
  .photo {
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    width: 160px;
    height: 160px;
    border: 8px solid #ff9b34;
    box-shadow: 0 0 4px #fff;
    position: relative;
    overflow: hidden;
  }
  .name {
    height: 70px;
    width: 100%;
    background-image: url(../assets/img/bg/hr.svg);
    background-size: 100% 100%;
    margin-top: 5px;
    p {
    font-size: 20px;
    padding-top: 3px;
    padding-left: 16px;
    font-weight: bold;
    color: #fff;
    } 
  }
  .bio {
    margin: 15px 0;
    width: 85%;
    p {
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    display: block;
    font-size: 14px;
    font-weight: 400;
    margin: 0 0 2px;
    }
  }
  .btn{
    align-self: flex-end;
  }
  .editors{
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    align-self: flex-end;
    .links{
      color: rgba(50, 50, 50, 0.8);
      transition: all 0.2s ease-in-out;
      &:hover{
        text-decoration: none;
        color: #fff;
      }
    }
  }
  @media (max-width: 767px){
    height: auto;
    // padding: 30px 0 0 0;
    padding-top: 30px;
    padding-bottom: 70px;
    background-position: 55%;
    overflow: hidden;
    .photo{
      width: 120px;
      height: 120px;
      border: 5px solid #ff9b34;
    }
    .name {
      background-image: url(../assets/img/bg/hr-sm.svg);
      padding: 0 10%;
    }
    .bio{
      display: none;
    }
    .btn{
      margin-right: 8%;
    }
    .editors{
      flex-direction: row;
      margin-top: 15px;
      width: 100%;
      justify-content: space-between;
      background-color: rgba(255, 255, 255, 0.8);
      position: absolute;
      bottom: 0;
      left: 0;
      &.menuSticky{
        position: fixed;
        top: 50px;
      }
      .links{
        flex: 1;
        text-align: center;
        display: block;
        padding: 13px 0;
        position: relative;
        // border-left: 1px solid rgba(#000, .3);
        &::before{
          content: '';
          height: calc(100% - 25px);
          width: 1px;
          background-color: rgba(#000, .3);
          position: absolute;
          left: 0;
        }
        &:first-child{
          border: none;
          &::before{
            content: none;
          }
        }
        &:hover{
          background-color: rgba(50, 50, 50, 0.8)
        }
      }
    }
  }
}
#detail-modal{
  .modal-title{
    font-weight: bold;
    svg{
      color: #999;
    }
  }
  .modal-body{
    .pre{
      margin-bottom: 5px;
    }
    p{
      color: #888;
      margin: 8px 0;
      border-bottom: 2px #eee solid;
      padding-bottom: 5px;
      svg{
        color: #fecc5b;
        font-weight: bold;
        width: 25px;
        font-size: 16px;
      }
      b{
        color: #222;
      }
      &.bd-n{
        border: none;
      }
    }
  }
}
</style>
